import React, { useState } from "react";
import "./Header.css";
import { Link } from "react-router-dom";
import Homepage from "../Homepage/Homepage";
import Us from "../Us/Us";
import Services from "../Services/Services";

function Header() {
  const [mobileDisplayed, setmobileDisplayed] = useState(true);

  const mobileDisplayer = () => {
    setmobileDisplayed(!mobileDisplayed);
  };

  const onChanger = () => {
    if (mobileDisplayed) {
      return burgerMenu;
    } else {
      return mobileMenu;
    }
  };

  let burgerMenu = (
    <div className="container-burger" onClick={mobileDisplayer}>
      <div className="line"></div>
      <div className="line"></div>
      <div className="line"></div>
    </div>
  );

  let mobileMenu = (
    <div className="mobileMenu">
      <ul className="navBarMobile">
        <button className="link-mobile" onClick={mobileDisplayer}>
          x
        </button>
        <Link
          className="link-mobile"
          to="/"
          Component={Homepage}
          onClick={mobileDisplayer}
        >
          Home
        </Link>
        <Link
          className="link-mobile"
          to="/us"
          Component={Us}
          onClick={mobileDisplayer}
        >
          Us
        </Link>
        <Link
          className="link-mobile"
          to="/services"
          Component={Services}
          onClick={mobileDisplayer}
        >
          Services
        </Link>
      </ul>
    </div>
  );

  return (
    <header className="header">
      <h1>Casupo</h1>
      {mobileDisplayed ? burgerMenu : mobileMenu}
      <ul className="navBar">
        <Link className="link" to="/" Component={Homepage}>
          Home
        </Link>
        <Link className="link" to="/us" Component={Us}>
          Us
        </Link>
        <Link className="link" to="/services" Component={Services}>
          Services
        </Link>
      </ul>
    </header>
  );
}

export default Header;
