import React, { Component } from "react";
import "./Services.css";
import expedition from "./expedition.jpg";
import travelPart from "./travelpart.jpg";
import alone from "./alone.jpg";

class Services extends Component {
  render() {
    return (
      <div className="servcont">
        <div className="container-ind" id="usCont">
          <div className="ContInner">
            <h1>Travel With us</h1>
            <p>
              Go with our guides and discover the most interesting facts about
              the place you visit and have the grates adventure of your life.
            </p>
          </div>
          <img className="imgExpedition" src={expedition} alt="expedition" />
        </div>

        <div className="container-ind" id="mixCont">
          <div className="ContInner">
            <h1>Travel Part</h1>
            <p>
              Our guides will take you to ceartain point and from there you are
              on your own, do you want to take the challengue?
            </p>
          </div>
          <img className="imgExpedition" src={travelPart} alt="travelPart" />
        </div>

        <div className="container-ind" id="aloneCont">
          <div className="ContInner">
            <h1>Travel With us</h1>
            <p>
              Go with our guides and discover the most interesting facts about
              the place you visit and have the grates adventure of your life
            </p>
          </div>
          <img className="imgExpedition" src={alone} alt="alone" />
        </div>
      </div>
    );
  }
}

export default Services;
