import React, { Component } from "react";
import "./Homepage.css";
import Services from "../Services/Services";
import explore from "./explore.jpg";
import { Link } from "react-router-dom";

class Homepage extends Component {
  render() {
    return (
      <div className="home">
        <div className="titleCont">
          <h1>Travel</h1>
          <p>We will take you to your best trip ever</p>
          <p>Are you ready for the trip of your life?</p>
          <br />
          <div className="containerButtom">
            <Link className="linkHome" to="/services" Component={Services}>
              Discover
            </Link>
          </div>
        </div>
        <div className="imgCont">
          <img src={explore} alt="travel" />
        </div>
      </div>
    );
  }
}
export default Homepage;
