import React, { Component } from "react";
import "./Us.css";

class Us extends Component {
  render() {
    return (
      <div className="usCon">
        <div className="missionCont">
          <h1>1) Who we are?</h1>
          <p>
            Casupo is a company founded in 1992, to take the tourist to the best
            and most exciting places around the world.
          </p>
        </div>
        <div className="missionCont">
          <h1>2) Our mission</h1>
          <p>Take you the traveler to the best time of your life.</p>
        </div>
        <div className="missionCont">
          <h1>3) Where to?</h1>
          <p>
            Tokio, Germany, London, Machu Pichu, New York, any place and
            anywhere you can think about we will take you.
          </p>
        </div>
      </div>
    );
  }
}

export default Us;
